.page-agenda-simply {
  padding: 0;
  md-tab-item{
    @media screen and (min-width: 768px) {
      max-width:33%;
      width:33%;
    }
  }

  md-tabs{
    background: none !important;

    md-pagination-wrapper{ min-width: 100% !important; }

    md-content  {
      padding: 0 !important;

      timeline{

        .timeline{
          margin: 0;
          padding-top: 20px;
          background: #fff;

          &:before{
            /* iPad H */
            @media screen and (max-width: 3000px) { left: 20px; /*background: red;*/ }

            /* iPad V */
            @media screen and (max-width: 768px) { left: 3.5%; /*background: lime;*/ }

            /* iphone6Plus */
            @media screen and (max-width: 415px) { left: 5.5%; /*background: yellow;*/ }

            /* iphone6 */
            @media screen and (max-width: 375px) { left: 6.5%; /*background: orange;*/ }

            /* iphone5 */
            @media screen and (max-width: 320px) { left: 5.8%; /*background: green;*/ }

            /* iphone4 */
            @media screen and (max-width: 320px) and (max-height: 480px) { /*background: maroon;*/ }
          }

          timeline-event{

            .timeline-event {
              margin-right: 16px;
              margin-bottom: 0;

              timeline-badge{
                background: $primary-color;
                width: 12px;
                height: 12px;
                /*box-shadow: 0 0 0 10px #fff,inset 0 2px 0 rgba(0,0,0,.08),0 3px 0 4px rgba(0,0,0,.05);*/
                box-shadow: 0 0 0 7px #fafafa,inset 0 2px 0 rgba(0,0,0,.08),0 3px 0 4px rgba(0,0,0,.05);

                /* iPad H */
                @media screen and (max-width: 3000px) { left: 40px; /*background: red;*/ }
                @media screen and (max-width: 1024px) { left: 4%; /*background: red;*/ }

                /* iPad V */
                @media screen and (max-width: 768px) { left: 6%; /*background: lime;*/ }

                /* iphone6Plus */
                @media screen and (max-width: 415px) { left: 4%; /*background: yellow;*/ }

                /* iphone6 */
                @media screen and (max-width: 375px) { left: 4%; }

                /* iphone5 */
                @media screen and (max-width: 320px) {}

                /* iphone4 */
                @media screen and (max-width: 320px) and (max-height: 480px) {}

                .timeline-badge{
                  font-size: 0.4em;
                }
              }

              timeline-panel{
                min-height: 48px;
                margin-bottom: 10px;
                margin-top: 34px;
                width: calc(94% - 45px);

                /* iPad H */
                @media screen and (max-width: 3000px) { margin-top: 14px; width: calc(99% - 35px); /*background: red;*/ }

                /* iPad V */
                @media screen and (max-width: 768px) { width: calc(98% - 35px); /*background: lime;*/ }

                /* iphone6Plus */
                @media screen and (max-width: 415px) { margin-top: 24px; width: calc(98% - 35px); /*background: yellow;*/ }

                /* iphone6 */
                @media screen and (max-width: 375px) {}

                /* iphone5 */
                @media screen and (max-width: 320px) { width: calc(98% - 35px); }

                /* iphone4 */
                @media screen and (max-width: 320px) and (max-height: 480px) {}

                &:after{
                  top: 10px;
                  left: -7px;
                  border-top: 8px solid transparent;
                  border-left: 0px solid #fff;
                  border-right: 8px solid #fff;
                  border-bottom: 8px solid transparent;
                }

                &:before{
                  top: 10px;
                  left: -9px;
                  border-top: 9px solid transparent;
                  border-left: 0px solid #ccc;
                  border-right: 9px solid #ccc;
                  border-bottom: 9px solid transparent;
                }

                .timeline-panel{
                  position: relative;

                  .time{
                    position: absolute;
                    top: -43px;
                    left: 0;
                    font-weight: bold;
                    padding-left: 30px;
                    background:{
                      image:url(../img/clock.svg);
                      position: center left;
                      repeat: no-repeat;
                      size: auto 83%;
                    }
                  }

                  .col-item {
                    position: relative;
                    padding: 0 0 5px;

                    &.marBottom20  { padding: 0 0 20px; }

                    &:last-child{
                      padding-bottom: 0;
                    }

                    .icon{
                      position: absolute;
                      top: 0;
                      left: 0;
                      width: 20px;
                      height: 24px;
                    }
                    .title_icon { margin-top: -4px; }
                    .title_text, .subtitle_text {
                      font-size: 1em;
                    }

                    .title_text {
                      text-transform: uppercase;
                      font-weight: bold;
                    }

                    .description_text {
                      font-size: 0.85em;
                      line-height: 1.3;
                      font-style: italic;
                      background: #e5e5e5;
                      padding: 10px!important;
                      border-radius: 3px;
                    }

                    .participant_text { text-decoration: underline; cursor:pointer;}

                    .text{
                      padding-left: 30px;
                      padding-bottom: 0;

                      p{
                        font-weight: normal;
                        margin: 0;
                      }

                      &.padding-left-none{
                        padding-left: 0;
                      }

                      .underline{
                        border-bottom: 1px dashed rgba(0,0,0,0.87);
                        display: inline-block;
                        padding: 0;
                      }
                    }
                  }
                }
              }
            }
          }
        }


      }




    }
  }

}
///////////////////////////////////////////////////
/* */

.page-agenda-simply {
  md-tabs.md-default-theme .md-tab.md-active, md-tabs .md-tab.md-active, md-tabs.md-default-theme .md-tab.md-active md-icon, md-tabs .md-tab.md-active md-icon, md-tabs.md-default-theme .md-tab.md-focused, md-tabs .md-tab.md-focused, md-tabs.md-default-theme .md-tab.md-focused md-icon, md-tabs .md-tab.md-focused md-icon {
    background-color: $pink-color!important;
    color: $primary-color2 !important;
  }

  md-tabs.md-default-theme .md-tab, md-tabs .md-tab{
    color: $primary-color !important;
    font-size: 15px!important;
  }
  md-ink-bar {
    display: none;
  }
}
