/* Butons */

.md-button.md-primary.md-raised{
  background-color: $primary-color !important;
}
.md-button.md-primary.md-default-theme span{
  color: $primary-color !important;
}

.md-button.md-primary{
  span{
    color: white !important;
  }
}

md-toast {
  position: absolute;
  z-index: 105;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(128, 128, 128, 0.8);
  display: block;

  .md-toast-content{
    margin: 10px;
    border-radius: 10px;
    padding-top: 16px;
    padding-bottom: 16px;
    align-items: flex-start;
    position: relative;
    max-height: 100%;

    &:before{
      display: none;
    }

    .content-thoats{
      padding-right: 10px;

      .message{

      }

      .buttons{
        //
        padding-top: 20px;
        text-align: center;
        padding-left: 15px;

        .button{
          padding: 0 7px;
          margin: 0 7px;
          background: $primary-color;
        }

        .go_pushes_button{

        }

        .cancel_push_button{

        }
      }
    }

    .close_button{
      float: right;
      margin: 0;
      padding: 0;
      background: url('../img/close_icon.svg') center center no-repeat;
      background-size: contain;
      width: 20px;
      display: block;
      height: 20px !important;
      line-height: 20px;
      margin-top: -10px;
    }
  }
}

/* Togles */
md-switch.md-checked .md-ink-ripple{
  background-color: $accent-color !important;
}

md-switch.md-checked .md-bar{
  background-color: rgba($accent-color, .5) !important;
}

/* Inputs */
md-input-container:not(.md-input-invalid).md-input-focused{
  .md-input{ border-color: $primary-color}
  label{ color: $primary-color}
}

/* Select */
md-select:not([disabled]):focus .md-select-value{
  border-bottom-color: $primary-color
}

md-select-menu md-option[selected] {
  color: $primary-color !important;
}

/* Radio */
md-radio-button:not([disabled]){
  &.md-checked .md-off {
    border-color: rgba($accent-color, 0.87);
  }
  .md-on {
    background-color: rgba($accent-color, 0.87);
  }
  &.md-primary{
    &.md-checked .md-off {
      border-color: rgba($primary-color, 0.87);
    }
    .md-on {
      background-color: rgba($primary-color, 0.87);
    }
  }
}

.md-checked:not([disabled]){
  &.md-primary .md-ink-ripple{
    color: rgba($primary-color, 0.26);
  }
  .md-ink-ripple {
    color: rgba($accent-color, 0.26);
  }
}

md-checkbox.md-default-theme.md-checked .md-icon, md-checkbox.md-checked .md-icon {
    background-color: $accent-color
}
/* tabs */

md-tabs.md-default-theme .md-tab.md-active,
md-tabs .md-tab.md-active,
md-tabs.md-default-theme .md-tab.md-active md-icon, md-tabs .md-tab.md-active md-icon,
md-tabs.md-default-theme .md-tab.md-focused,
md-tabs .md-tab.md-focused,
md-tabs.md-default-theme .md-tab.md-focused md-icon,
md-tabs .md-tab.md-focused md-icon {
    color: $primary-color;
}

md-tabs.md-default-theme md-ink-bar, md-tabs md-ink-bar {
    color: $accent-color;
    background: $accent-color;
}

/* range */
md-progress-linear, md-progress-linear.md-default-theme {
  .md-container {
    background-color: mix($primary-color, white, 30%);
  }
  .md-bar {
    background-color: $primary-color;
  }
}