.ripple {
  position: relative; }
  .ripple:active > .wave {
    animation: ripple 0.25s; }
  .ripple .wave {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: radial-gradient(circle, #000 10%, transparent 10.01%);
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 0 0;
    top: 0;
    left: 0;
    transform: scale(0);
    opacity: 0; }

@keyframes ripple {
  0% {
    transform: scaleX(0); }
  50% {
    transform: scaleX(1);
    opacity: 0.3; }
  100% {
    transform: scaleX(1);
    opacity: 0;
    background-size: 1000% 1000%; } }

