.questions_to_orgs-page {
  height: 100%;
  overflow-y: hidden;
  padding: 10px;
  background: #fff;

  .md-button.md-primary.md-raised {
    background-color: #8cc63f!important;
  }

  .avatar {
    width: 300px;
    height: 300px;
    display: block;
    vertical-align: top;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    background: url("../img/avatar.svg") center no-repeat;
    background-size: cover;
    margin: 0 auto;
    border: 1px solid #8cc63f;

    @media screen and (max-width: 470px) {
      width: 155px;
      height: 155px;
    }

    .mask {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 20px;
      border-radius: 50%;
      font-size: 3.5em;
    }
  }

  .question-form{
    max-width: 640px;
    width: 100%;
    margin: 20px auto 0;

    @media screen and (max-width: 470px) { max-width: 450px; }

    .feedback-container{
      text-align: center;

      md-checkbox {
        width: 100%;
        text-align: left;

        @media screen and (max-width: 470px) { max-width: 320px; }
      }

      textarea {
        margin-bottom: 10px;
        padding: 10px;
        border: 1px solid #8cc63f;
        width: 100%;
        height: 105px;
        margin-bottom: 10px;

        @media screen and (max-width: 470px) { max-width: 320px; }
      }

     /* input { height: 35px; }*/

    }
    md-input-container {
      width: 100%;
      /*margin: 10px auto;*/
      text-align: left;

      @media screen and (max-width: 470px) { max-width: 320px; }
    }
    .ask{
      margin: 0 auto;
      display: block;
      width: 100%;
      max-width: 290px;
      padding: 5px 12px;
    }
    .result-form{
      text-align: center;
      margin-top: 15px;
      color: $primary-color;
      display:block;
      opacity: 0;
    }
  }



    .title{
      vertical-align: top;
      display: block;
      margin-top: 10px;
      font-size: 22px;
      line-height: 1.3em;
      text-align: center;
    }

    .info{
      padding: 0;
      ul{
        padding-left: 15px;
        li{
          list-style-type: disc;
        }
      }
    }

}
