.news-page{
  background: none;
  padding: 16px;
  height: 100%;
  overflow: hidden;
  overflow-y: scroll;
  position: relative;

  .head{
    font-size: 1.2rem;
    font-weight: bold;
    padding-bottom: 16px;
  }

  .news-list {
    padding: 35px 0px 0;
    @media(min-width: 1200px){
      padding: 65px 0px 0;
    }
    .news-list-inner {
      border-radius: 3px;
      border: 1px solid $primary-color;
      margin: 1em 0;

      .head{
        background: $primary-color;
        padding: 1em;
        font-size: 1.2em;
        font-weight: bold;
        margin: 0;
        color: #fff;
        position: relative;

        .icon_{
          max-width: 32px;
          display: inline-block;
          margin-right: 10px;
        }

        .delete {
          display: inline-block;
          position: absolute;
          right: 0;

          img {
            width: 20px;
            margin: 0 10px;
          }
        }
      }

      .card{
        padding: 1em;

        .author{
          text-align: right;
          font-size: 0.9em;
          color: $primary-color;
        }
        .time:before{
          //content: ' : ';
        }

        .time{
          color: #959595;
          //font-style: italic;
          overflow: hidden;
          //font-weight: bold;
          font-size: 14px;
          padding-top: 5px;
          width: 100%;

        }

        .desc{
          overflow: hidden;
          padding-top: 10px;
          padding-bottom: 0.6em;

          p{
            margin-bottom: 10px;
            margin-top: 0;
          }
        }

        .photo-list{
          padding-bottom: 1em;
        }


        .pdf-area{
          padding: 0em 0 0.3em;
          border: none;

          .title{
            margin-left: 0.5em;
            height: 45px;
            line-height: 45px;
            display: inline-block;
            vertical-align: top;
            color: #000;
            text-decoration: underline;
          }

          a{
            display: inline-block;
            text-decoration: none;
            border: none;

            img{
              display: block;
              height: 45px;
            }
          }
        }

        .link-more-area{

          .link-more-label{
            color: #000;
            text-decoration: underline;
          }
        }
      }

    }

    .activities {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;

      div {
        cursor:pointer;
        filter: grayscale(1);
        &.liked {
          filter: grayscale(0);
        }
        span {
          color: $primary-color;
          line-height: 1;
        }
        img, span {
          display: inline-block;
          vertical-align: middle;
        }
        img {
          width: 20px;
          margin: 0 10px;
        }
      }
    }

  }

  .add-news-item-btn{
    margin-bottom: 16px;
  }

  .add-news-feed-item-dialog{
    position: relative;
    z-index: 2000;

    .md-dialog-content{
      padding: 16px !important;
    }

  }
}

.dialog-field{
  border: 1px solid $primary-color;
  display: block;
  width: 100%;
  background: #fff;
  padding: 5px;
  margin-bottom: 16px;

  &.new-news-body{
    min-height: 70px;
  }
}

.md-dialog-content .photos {
  > p {
    color: $primary-color;
    margin-bottom: 0;
  }
  .photo-block {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 10px;
    div {
      width: 58px;
      margin: 1px;
      a {
        display: block;
        border-bottom: none;
        width: 58px;
        height: 58px;
      }
      p {
        background: red;
        line-height: 1;
        padding: 0;
        margin: 0;
        text-align: center;
        text-transform: uppercase;
        img {
          height: 20px;
          transform: rotate(45deg);
        }
      }
    }
  }
}

.md-dialog-content .buttons {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;

  button {
    background: #ffffff;
    border: 1px solid $primary-color;
    color: $primary-color;
    border-radius: 4px;
    display: block;
    align-items: center;
    width: 40%;
    margin: 0 5%;
    padding: 5px 0;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;

    &[disabled] {
      filter: grayscale(1);
    }
    img, span {
      display: inline-block;
      vertical-align: middle;
    }
    img {
      width: 20px;
      margin: 5px;
    }
    span {
      font-size: 12px;
    }
    @media screen and (max-width: 570px) {
      flex-wrap: wrap;
      align-items: flex-start;
      img {
        width: 25px;
        margin: 0 auto;
      }
      span {
        width: 100%;
        font-size: .6em;
        line-height: 1;
      }
    }
  }
}

.comments {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 100%;
  left: 0;
  z-index: 999!important;
  overflow: hidden;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.5);
  background: #f0f0f0;
  transition: top 0.3s linear;
  will-change: top;
  padding: 1%;
  display: flex!important;

  &.show {
    top: 0;
  }
  .comment-block {
    background: #fff;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.5);
    border-radius: 4px;
    padding: 10px 10px 0;
    position: relative;
    width: 98%;
    max-width: 650px;
    margin: 0 auto;
    height: 100%;
    .messages {
      height: calc(100% - 45px);
      overflow: hidden;
      margin-bottom: 5px;
      overflow-y: scroll;
      div {
        padding: 15px 0 10px;
        border-bottom: 1px solid #d7d7d7;
        position: relative;
        md-menu {
          position: absolute;
          right: 0;
          top: 10px;
          .open {
            filter: grayscale(1)
          }
        }
        &:last-child {
          border-bottom: none;
        }
        p {
          margin: 4px 0;
          font-size: 1em;
          line-height: 1.1;
          &.author {
            color: $primary-color;
            font-weight: 600;
          }
          &.date {
            font-size: 0.7em;
            color: #d7d7d7;
          }
          &.message {
            padding: 5px 0;
          }
        }
      }
    }
    .send-block {
      height: 40px;
      display: flex;
      align-items: center;
      border-top: 1px solid #d7d7d7;
      input {
        width: 100%;
        height: 30px;
        padding: 0 8px;
        border: none;
      }
      .send, .editIcon {
        width: 30px;
        margin: 0 4px;
        min-width: 30px;
        height: 30px;
        background: url(../img/send.svg) no-repeat center;
        background-size: contain;
      }
      .editIcon{
        background: url(../img/delete.svg) no-repeat center;
        background-size: cover;
        width: 20px;
        min-width: 20px;
        height: 20px;
        filter: grayscale(1);
        transform: rotate(45deg);
      }
    }
  }
}

