.fullHeightDinner{
  min-height:100%;
  /*background: url(../img/dinner/Logo1_01.png) center no-repeat!important;
  background-size:cover!important;

  @media screen and (max-width: 770px) {
    background: url(../img/dinner/Logo2_01.png) center no-repeat!important;
    background-size:cover!important;
  }*/
}
.dinner-page{
  font-size: 1.2em;
  @media screen and (max-width: 570px) {
    font-size: 1.1em;
  }

  @media screen and (max-width: 320px) {
    font-size: 1em;
  }
  padding:8px;

  .aboutDinner{
    .pic{
      img{
        width:100%;
      }
    }
    .text{
      text-align: justify;
    }
  }

  .center { text-align: center; }

  .dresscode p { margin: 5px 0; }

  .btns{
    text-align: center;

    .btnMore{
      color: #000;
      background:$pink-color;
      padding:0 5px;
      font-size: .75em;
      width: 49%;
      margin: 0;
    }
  }

  .table_buttons {

    .md-button {
      display: block;
      text-align: center;
      background: #ef2427;
      width: 98%;
      margin: 10px auto;
      color: #fff;
    }

    ol {
      padding-left: 20px;

      li {
        margin: 5px 0;
        list-style-position: inside;
      }
    }
  }

  .pic {
    width: 100%;
    margin: 8px auto;
    max-width: 700px;
    display: block;
    box-shadow: 0 0 8px rgba(0,0,0,.5);
    border-radius: 5px;
  }

  .items{
    min-height:100%;
    width:320px;
    @media screen and (max-width: 570px) {
      width:250px;
    }
    @media screen and (max-width: 320px) {
      width:230px;
    }
    margin:0 auto;
    display:flex;
    align-content: center;
    flex-wrap: wrap;
    .line{
      width:100%;
      margin:3px auto;
      display:flex;
      align-items: center;
      padding:16px;
      cursor:pointer;
      background: rgba(255, 255, 255, 0.75);
      border-radius: 5px;
      img{
        flex-shrink: 0;
        width:50px;
        height:50px;
        @media screen and (max-width: 320px) {
          width:40px;
          height:40px;
        }
      }
      .title{
        margin-left:8px;
        font-size: 0.9em;
        font-weight: bold;
        line-height: 1.1em;
        span{
          border-bottom: 1px dashed $text-color;
        }
      }
    }
  }

  .section{
    &.cards{
      .welcome p{
        text-align:center;
      }
      @media screen and (max-width: 1024px) {
        font-size: 0.9em;
      }
      .item{
        margin:8px 0;
        .title{
          padding:16px;
          background: $green-color;
          font-weight: bold;
          color:$text-color;
          //text-align:center;
          line-height: 1.1em;
        }
        .text{
          padding:16px;
          font-size:0.9em;
          line-height: 1.5em;
          a{
            word-break: break-all;
          }
        }
      }
    }
  }
}