.fullHeight{
  height:100%;
}
.info-page{
  font-size: 1.2em;
  @media screen and (max-width: 570px) {
    font-size: 1.1em;
  }

  @media screen and (max-width: 320px) {
    font-size: 1em;
  }
  padding:8px;

  //============scheme================
  .table_buttons {

    .md-button {
      display: block;
      text-align: center;
      background: #ef2427;
      width: 98%;
      margin: 10px auto;
      color: #fff;
    }

    ol {
      padding-left: 20px;

      li {
        margin: 5px 0;
        list-style-type: none;
      }
    }
  }

  .pic {
    width: 100%;
    margin: 8px auto;
    max-width: 700px;
    display: block;
    box-shadow: 0 0 8px rgba(0,0,0,.5);
    border-radius: 5px;
  }
  //==================================

  .items{
    min-height:100%;
    width:310px;
    @media screen and (min-width: 768px) {
      width:500px;
    }
    @media screen and (min-width: 1100px) {
      width:600px;;
    }
/*    width:320px;
    @media screen and (max-width: 570px) {
      width:250px;
    }
    @media screen and (max-width: 320px) {
      width:230px;
    }*/
    margin:0 auto;
    display:flex;
    align-content: center;
    flex-wrap: wrap;
    .weather-line-inner{
      display: flex;
      //flex-wrap: nowrap;
      justify-content: left;
      align-items: center;
      text-decoration: none;
      border: none;
      margin-top:20px!important;
      .icon_{
        margin-right:10px;
        width:50px;
        height:50px;
        @media screen and (max-width: 320px) {
          width:40px;
          height:40px;
        }
      }
      .description{
        font-weight: bold;
        font-size:1em;
        line-height: 1em;
        .temp{
          font-size:1.2em;
        }
      }
    }
    a, a:hover{
      color:#000;
      text-decoration: none;
      border:none;
    }
    .line{
      width:100%;
      margin:0 auto;
      display:flex;
      align-items: center;
      padding:10px;
      cursor:pointer;
      @media screen and (min-width: 768px) {
        font-size: 1.1em;
      }
      img{
        flex-shrink: 0;
        width:40px;
        height:40px;
        @media screen and (max-width: 320px) {
          width:30px;
          height:30px;
        }
        @media screen and (min-width: 768px) {
          width:50px;
          height:50px;
        }
      }
      .title{
        margin-left:8px;
        font-size: 0.9em;
        font-weight: bold;
        line-height: 1.2em;
        span{
          border-bottom: 1px dashed $text-color;
        }
      }
    }
  }

  .section{
    &.orgs, &.freetime {
      .welcome{
        padding:0 16px;
        line-height:1.1em;
        text-align: center;
      }
      .org{
        padding:16px;
        display:flex;
        flex-direction: row;
        flex-wrap:wrap;
        align-content: center;
        align-items: center;
        border-bottom: 1px solid $primary-color;

        .md-button {
          margin-left: 0;
          color: #fff;
          background: #ed1d24!important;
        }

        .photo{
          flex-shrink: 0;
          width:70px;
          height:70px;
          border:1px solid $primary-color;
          border-radius: 50%;
          margin-right:8px;
          @media (min-width: 500px) {
            width:85px;
            height:85px;
          }
          @media (min-width: 1024px) {
            width: 120px;
            height: 120px;
          }
          .mask{
            width:100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size:1.4em;
            color: $primary-color;
            font-weight: bold;
          }
        }
        .info{
          width:calc(100% - 85px);
          @media (min-width: 500px) {
            width:calc(100% - 95px);
          }
          @media (min-width: 1024px) {
            width:calc(100% - 130px);
          }
          .name{
            color:$primary-color;
            font-weight: bold;
            line-height: 1em;
            font-size:1em;
          }
        }
        .position{
          margin-top:16px;
          width:100%;
          display: flex;
          align-items:flex-start ;
          font-size:0.9em;
          margin-bottom:8px;
          line-height: 1.1em;
          img{
            width:20px;
            flex-shrink: 0;
            margin-right:5px;
          }
        }
        .phone{
          width:100%;
          display: flex;
          align-items:flex-start ;
          margin-bottom:8px;
          img{
            width:20px;
            flex-shrink: 0;
          }
          .tel{
            font-size:0.9em;
            margin-left:5px;
            p{
              margin:0;padding: 0;
            }
          }
        }
      }
    }
    &.hotel{
      @media screen and (max-width: 1024px) {
        font-size: 0.9em;
      }
      .item{
        .title{
          padding:16px;
          text-align: center;
          font-weight: bold;
          color:$text-color;
          font-size:1.2em;
          //text-align:center;
          line-height: 1.1em;
        }
        .pic{
          width:100%;
          margin:8px auto;
          max-width:700px;
          display:block;
          box-shadow: 0 0 8px rgba(0,0,0,0.5);
          border-radius: 5px;
        }
        a{
          height:50vw;
          max-height:400px;
        }
        .btns{
          text-align: center;
          .btnMore{
            color: #000;
            background:$pink-color;
            padding:0 5px;
            font-size: .75em;
            width: 49%;
            margin: 0;
          }
        }
        .text{
          padding:0 16px;
          font-size:1.1em;
          line-height: 1.3em;
          @media screen and (max-width: 1200px) {
            font-size:1em;
          }
        }
        .maps{
          display:flex;
          width:100%;
          max-width:500px;
          margin:0 auto;
          padding-bottom:70px;
          justify-content: center;
          .map{
            text-align: center;
            flex-grow:1;
          }
        }
      }
    }
    &.cards{
      @media screen and (max-width: 1024px) {
        font-size: 0.9em;
      }
      .item{
        margin:8px 0;
        .title{
          padding:16px;
          background: $green-color;
          font-weight: bold;
          color:$text-color;
          //text-align:center;
          line-height: 1.1em;
        }
        .text{
          padding:16px;
          font-size:0.9em;
          line-height: 1.5em;
          a{
            word-break: break-all;
          }
        }
      }
    }
    &.more{
      display:flex;
      flex-wrap:wrap;
      justify-content:center;
      &.halls{
        justify-content: center;
        .item{
          width: 100vw!important;
          height:60vw;
          max-width:100vw;
          max-height:80vh;
        }
      }
      >.item{
        display:inline-block;
        width:47vw;
        height:47vw;
        max-width:300px;
        max-height:300px;
        background: #9e9e9e;
        margin:0 8px 8px 0;
        a{
          display:block;
          width:100%;
          height:100%;
          .img{
            width:100%;
          }
          .title{
            text-align:center;
          }
        }
      }

      .form{
        padding: 16px 28px;
        background: #f0f0f0;
        border-radius: 3px;

        .item{
          margin-bottom:5px;
          .inp{
            text-align: left;
            margin:10px 0 0 0;
            width:70%;
            max-width:800px;
            min-width:250px;
          }
          .title{
            width:100%;
          }
          .text{
            font-weight: bold;
          }
          .active{
            filter:grayscale(0)!important;
          }
          .pic{
            width:50px;
            height:50px;
            -webkit-filter: grayscale(1);
            filter: grayscale(1);
            cursor:pointer;
          }
        }
        .btnSendTravelCard{
          //color:#fff;
          background: $green-color;
          margin:16px 0;
        }
      }
    }
    &.guests{
      .guestsList{
        .org{
          cursor:pointer;
        }
      }
      .guestInfo{
        .guestName{
          font-size: 1.5em;
          text-align: center;
          color:$primary-color;
          font-weight: bold;
          margin-top:16px;
          margin-bottom: 8px;
        }
        .guestPosition{
          text-align: center;
          font-weight: bold;
          margin-bottom:16px;
          line-height: 1.1em;
        }
        .guestPhoto{
          flex-shrink: 0;
          width:25vh;
          height:25vh;
          margin:0 auto;
          min-width:150px;
          min-height:150px;
          border:1px solid $primary-color;
          border-radius: 50%;
          .mask{
            width:100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size:4em;
            color: $primary-color;
            //font-weight: bold;
          }
        }
        .guestInterview{}
      }
    }
    &.business_scheme{
      text-align:center;
      .active{
        background: $primary-color!important;
        color:#fff!important;
      }
      .btnSector{
        color:#fff;
        padding:5px 10px;
        background: $gray-color;
        color:#000;
      }
    }
  }

}