.speakers-page{
  font-size: 1.2em;
  color:$text-color;
  @media screen and (max-width: 570px) {
    font-size: 1em;
  }

  @media screen and (max-width: 320px) {
    font-size: 0.9em;
  }
  .speakers{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    .speaker{
      cursor:pointer;
      padding:16px;
      background: url(../img/next.svg) 98% center no-repeat;
      background-size: 5%;
      display: flex;
      width:100%;
      align-items: center;
      flex-direction: row;
      @media(min-width: 768px){
        width:47%;
      }
      .photo{
        flex-shrink: 0;
        width:85px;
        height:85px;
        border:1px solid $primary-color;
        border-radius: 50%;
        margin-right:8px;
        @media (min-width: 1024px) {
          width: 120px;
          height: 120px;
        }
        .mask{
          width:100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size:1.5em;
          color: $primary-color;
          font-weight: bold;
        }
      }
      .info{
        width:80%;
        .name{
          font-size:1.1em;
          color: $primary-color;
          font-weight: bold;
          margin-bottom:16px;
        }
        .position{
          display: flex;
          align-items: flex-start;
          padding-right: 2%;
          line-height:1.2em;
          img{
            flex-shrink: 0;
            width:20px;
            margin-right:5px;
          }
        }
      }
    }
  }
  .speaker_info{
    .avaname{
      width:100%;
      text-align: center;
      padding:16px;
      .photo{
        flex-shrink: 0;
        width:25vh;
        height:25vh;
        margin:0 auto;
        min-width:150px;
        min-height:150px;
        border:1px solid $primary-color;
        border-radius: 50%;
        .mask{
          width:100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size:4em;
          color: $primary-color;
          //font-weight: bold;
        }
      }
      .name{
        font-size:1.3em;
        color: $primary-color;
        font-weight: bold;
        margin:8px;
      }
      .position{
        display: flex;
        align-items: flex-start;
        justify-content: center;
        img{
          flex-shrink: 0;
          width:20px;
          margin-right:5px;
        }
      }
    }
    .question{
      padding:0 16px;
      .title{
        font-weight: bold;
        margin:5px 0;
      }
      textarea{
        width:100%;
        height:150px;
        border:1px solid $primary-color;
        border-radius: 10px;
        padding:5px;
      }
      .sendQuestion{
        background: $pink-color;
        margin:8px 0 16px 0;
      }
    }
    .bio{
      padding:0 16px 16px 16px;
    }
  }
}