.participants-page{
  font-size: 1.1em;
  min-height:100%;
  @media screen and (max-width: 570px) {
    font-size: 1em;
  }

  @media screen and (max-width: 320px) {
    font-size: 0.9em;
  }

  .participants{
    .wrapper{
      width:100%;
      display:flex;
      flex-wrap:wrap;
      justify-content: space-between;
      @media screen and (max-width: 470px) {
        justify-content: center;
      }
    }
    md-card{
      padding:8px;
      min-height:150px;
      display:flex;
      flex-direction: row;
      align-items: center;
      width:49%;
      min-width:300px;
      margin:16px auto;
      cursor:pointer;
      background: url(../img/next.svg) 98% center no-repeat;
      background-size: 5%;
      border-bottom:1px solid $primary-color;
      @media screen and (max-width: 576px) {
        width:100%;
      }
      .photo{
        flex-shrink: 0;
        width: 85px;
        height: 85px;
        min-width:50px;
        min-height:50px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        border: 1px solid $primary-color;
        background: url("../img/avatar.svg") center no-repeat;
        background-size: cover;
        margin: 0 2% 0 0;
        @media screen and (min-width: 1200px) {
          width: 120px;
          height: 120px;
        }
        @media screen and (min-width: 570px) {
          width: 100px;
          height: 100px;
        }
        @media screen and (max-width: 320px) {
          width: 75px;
          height: 75px;
        }

        .mask{
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 2em;
          color:$primary-color;
          border-radius: 50%;
        }
        /*        @media screen and (max-width: 470px) {
                  width: 70px;
                  height: 70px;
                }*/

      }
      .info{
        margin-right:6%;
        .item{
          display:flex;
          align-items: flex-start;
          margin-bottom:5px;
          img{
            width:20px;
            height:20px;
            flex-shrink: 0;
          }
          .text{
            margin-left:5px;
            line-height: 1em;
            &.status{
              //word-break: break-all;
            }
          }

        }

        .fio{
          font-size:1em;
          line-height:1.1em;
          font-weight:bold;
          color:$primary-color;
          margin-bottom:10px;
        }
        .mood{
          margin-top:10px;
          margin-bottom:0!important;
        }
        .position{
          font-size:1em;
          font-weight:bold;
          color:#7d7d7d;
          line-height: 1.1em;
        }
        .region{
          color:#7d7d7d;
          font-size:0.9em;
        }
      }
    }
  }

  .participants_info{
    width:100%;
    min-height:100%;
    display:flex;
    align-content: flex-start;
    flex-wrap:wrap;
    .avaname{
      width:100%;
      text-align:center;
      .photo{
        width:25vh;
        height:25vh;
        margin:0 auto;
        min-width:150px;
        min-height:150px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        border: 1px solid $primary-color;
        background-size: cover;
        .mask{
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 5em;
          color:$primary-color;
          border-radius: 50%;
        }
      }
      .fio{
        color: $primary-color;
        font-weight:bold;
        margin-top:5px;
        margin-bottom:30px;
        font-size:1.3em;
      }
    }
    .info{
      width:100%;
      .item{
        margin-bottom:8px;
        display:flex;
        align-items: flex-start;
        img{
          width:25px;
          height:25px;
          flex-shrink: 0;
        }
        .text{
          margin-left:5px;
          line-height:1.1em;
          overflow-x: auto;
        }
        &.mood{
          padding-top:15px;
          border-top:1px solid $primary-color;
          margin-top:15px;
        }
      }
    }
  }
}