$laptop_width:1200px;
*{
  box-sizing: border-box;
  outline: none !important;
}
[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}
html{
  background-color: #fff !important;
}
body{
  background-color: #fff !important;
  font-family: $primary-font;
  line-height: 1.5em;
  margin: 0;
  opacity: 0;
  &.active {
    transition: 2s;
    opacity: 1;
  }
  newsFeedBlock{
    display: none;
    width:100%;
    height:60px;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    left:0;
    top: 56px;
    background: #f1f1f1;
    z-index:10;
    border-bottom:1px solid $pink-color;
    border-right:1px solid $pink-color;
    border-left:1px solid $pink-color;
    //filter: drop-shadow(0px 0px 10px #fff);
    @media(min-width: 1200px){
      width:$laptop_width;
      left:calc(50% - 600px);
    }
    &.ios{
      top: 76px;
    }
    .add-news-item-label{
      background-size: cover;
      font-size:0.9em;
      width: calc(50% - 8px);
      margin-right:8px;
      text-align:center;
      background: $pink-color;
      margin-left:5px;
      padding:5px 3px;
      border-radius:5px;
      max-width:155px;
      cursor:pointer;
    }
    .checkbox-filter-org{
      width:50%;
      margin: 0!important;
      font-size:0.9em;
      line-height:1.1em;
      max-width:300px;
    }

  }
}

header{
  background-color: $primary-color;
  color: white;
  font-size: 20px;
  box-shadow: 0 0 8px rgba(0,0,0,0.5);
  height: 56px;
  .header-inner{
    width:100%;
    @media(min-width: 1200px){
      max-width: $laptop_width;
      margin:0 auto;
    }
  }
  &._hidden {
    display: none;
  }
  &.search{
    background-color: white;
    input{
      margin-top: -20px;
    }
  }
  &.ios{
    padding-top: 20px;
    height: 76px;
  }
  div.icon{
    width: 56px;
    height: 56px;
    display: inline-block;
    background: $primary-color url('../img/left_menu_icon_white.svg') center no-repeat; background-size: 50%;
  }
  button.icon {
    width: 56px;
    height: 56px;
    min-width: 56px;
    padding: 0;
    margin: 0;
    max-width: 56px;
    background: url('../img/left_menu_icon_white.svg') center no-repeat; background-size: 50%;
  }
  .header-items {
    max-height: 56px;
    display: flex;
    justify-content: space-between;
    .left-items {
      text-align: left;
      &>* {
        display: inline-block;
        vertical-align: middle;
      }
      span{
        //margin-left: 20px;
        @media screen and (max-width: 370px) {
          font-size: 0.8em;
        }
      }
    }
    .right-items {
      text-align: right;
      &>* {
        display: inline-block;
        vertical-align: middle;
      }
      button {
        &.search-icon {
          background-image: url('../img/search_icon_white.svg');
        }
        &.back-icon {
          background-image: url('../img/back_icon_white.svg');
        }
        &.edit-icon {
          background-image: url('../img/edit_icon_white.svg');
        }
        &.done-icon {
          background-image: url('../img/done_icon_white.svg');
        }
        &.close-search-icon {
          background-image: url('../img/close_search_icon.svg')
        }
        &.moderation-icon {
          background-image: url('../img/moderation.svg')
        }
      }
      div {
        cursor:pointer;
        &.search-icon {
          background-image: url('../img/search_icon_white.svg');
        }
        &.back-icon {
          background-image: url('../img/back_icon_white.svg');
        }
        &.edit-icon {
          background-image: url('../img/edit_icon_white.svg');
        }
        &.done-icon {
          background-image: url('../img/done_icon_white.svg');
        }
        &.close-search-icon {
          background-image: url('../img/close_search_icon.svg');
        }
        &.moderation-icon {
          background-image: url('../img/moderation.svg');
        }
        &.agenda2-icon {
          background-image: url('../img/custom/agenda2.svg')!important;
        }
      }
    }
    .left-menu-icon {
      cursor:pointer;
      background-image: url('../img/left_menu_icon_white.svg');
      align-self: flex-start;
    }
  }
}

main{
  background: transparent;
  height: calc(100vh - 56px);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  position: relative;
  &.ios{
    height: calc(100vh - 76px);
  }
  @media(min-width: 1370px){
    max-width:$laptop_width!important;
    margin:0 auto;
  }
}

.pagecontent {
  background: transparent;
  min-height: 100%;
    &>md-content {
        background: transparent;
        -webkit-animation: 0.5s fade-in;
        -moz-animation: 0.5s fade-in;
        -o-animation: 0.5s fade-in;
        animation: 0.5s fade-in;
    }
}

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}
@-moz-keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}
@-webkit-keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}
@-moz-keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}
@-webkit-keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

nav{
  position: fixed;
  top: 0;
  transform: translateZ(0);
  will-change: left;
  left: -260px;
  height: 100vh;
  min-width: 280px;
  transition: left.2s ease;
  z-index: 25;
  &.ios{
    padding-top: 20px;
  }
  .content{
    background-color: white;
    height: 100%;
    width: 260px;

    .logo{
      background: #fff url($event-logo) center no-repeat;
      background-size: 55%;
      height: 56px;
      border-bottom: 1px solid $primary-color;
      display: block;
      justify-content: center;
      align-items: center;
      outline: none;
      text-align: center;
      img {
        max-width: 67%
      }
    }
  }

  .shadow{
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateZ(0);
    will-change: opacity;
    position: absolute;
    top: 0; right: 0;
    margin-right: -1980px;
    width: 2000px;
    height: 100%;
    display: none;
  }

  &.move{
    transition: none;
  }
  &.move .content{
    transition: none;
  }

  ul{
    clear: both;
    margin: initial;
    list-style-type: none;
    padding-left: 0;
    height: calc(100vh - 65px);
    overflow-y: auto;
    padding-bottom: 20px;
    li{
      display: block;
      padding: 10px 0;
      font-size: 14px;
      cursor: pointer;
      outline: none;

      img{
        max-width: 28px;
        margin: 0 6px 0 10px;
        vertical-align: middle;
        padding: 4px;
        background: rgba(179,179,179,.24);
        border-radius: 3px;
      }
      span {
        vertical-align: middle;
        font-size: 1.2em;
      }
    }
  }
}

md-tabs:not(.md-no-tab-content):not(.md-dynamic-height) {
    min-height: calc(100vh - 56px);
}

.index_md_content {
  padding: 0 !important; height: 100% !important;
}
.s404 {
  padding: 0 15px;
}
.scroll-to-app-button{
  box-shadow: 0 0 8px #fff;
  position: fixed;
  opacity: 0;
  width: 64px;
  height: 64px;
  bottom: -100px;
  right: 11px;
  border-radius: 100%;
  z-index: 1;
  cursor: pointer;
  transition: bottom.4s ease, opacity.6s ease;
  will-change: left, top, bottom, right, opacity;
  outline: none;
  background: rgba(0,0,0,0.3) url('../img/scrolltop.svg') center no-repeat;
  @media(min-width: 1200px){
    right:calc(50% - 580px);
  }
  &.show{
    opacity: 1;
    bottom: 11px;
  }
}
.search_block + main {
  padding-top:35px
}

.search_block {
  position: absolute;
  max-height: inherit;
  display:none;
  //display: flex;
  align-items: center;
  z-index: 2;
  width: 100%;
  @media(min-width: 1200px){
    max-width:$laptop_width;
    left:calc(50% - 600px);
  }
  input{
    width: 100%;
    font-size:15px;
    color:#333333;
    height: 35px;
    padding-left: 15px;
    border:1px solid #b6b6b6;
    transition: width ease-out 0.1s;
    border-radius:0;
  }
  .clear_input{
    color:white;
    background-color: $primary-color;
    border:1px solid #b6b6b6;
    font-size: 16px;
    padding: 5px 10px;
    text-transform: uppercase;
    height: 35px;
    transition: width ease-out 0.1s;
    cursor: pointer;
  }
}
.loading {
  position: fixed;
  z-index: 10000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-align: center;
  background-color: rgba(255,255,255,0.6);
  div  {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    img {
      max-width: 200px;
      display: block;
    }
    h1 {
      font-size: 1em;
    }
  }

  &.hide {
    display: none !important;
  }
}
.ep-button{   /* animation effects storage is here https://daneden.github.io/animate.css/ */
  width: 70%;
  margin: 0 auto 20px;
  color: #000;
  font-weight: bold;
  float: left;
  clear: left;
  display: flex;
  text-align: center;
  justify-content: center;

  $duration: 1s;
  $function: linear;
  $count: infinite;

  &.rotate{
    animation:rotate $duration $function $count;
    -webkit-animation:rotate $duration $function $count;
  }

  @keyframes rotate {
    0%      { transform:rotate(120deg); }
    25%     { transform:rotate(240deg); }
    50%     { transform:rotate(360deg); }
  }

  &.bg-change{
    background-color: #fff;
    animation: bg-change $duration $function $count;
    -webkit-animation: bg-change $duration $function $count;
  }

  @keyframes bg-change {
    0%       { background-color: lime; }
    25%      { background-color: magenta; }
    50%      { background-color: yellow }
  }

  &.rounded{
    border-radius: 50%;
    animation: animated-rounded $duration $function $count;
    -webkit-animation: animated-rounded $duration $function $count;
  }

  @keyframes animated-rounded {
    0%       { border-radius: 0%; }
    10%      { border-radius: 50%; }
    50%      { border-radius: 0%; }
  }

  &.opacity{
    opacity: 0;
    animation: animated-opacity $duration $function $count;
    -webkit-animation: animated-opacity $duration $function $count;
  }

  @keyframes animated-opacity {
    0%       { opacity: 1; }
    25%      { opacity: 0.5; }
    50%      { opacity: 1; }
  }

  &.pulse{
    animation:pulse $duration $function $count;
    -webkit-animation:pulse $duration $function $count;
  }

  @keyframes pulse {
    0%      { transform:scale(1); }
    25%     { transform:scale(2); }
    50%     { transform:scale(1); }
  }

  &.shadow{
    animation:shadow $duration $function $count;
    -webkit-animation:shadow $duration $function $count;
  }

  @keyframes shadow {
    0%      {
      -webkit-box-shadow: 0px 0px 55px -45px rgba(0,0,0,0.75);
      -moz-box-shadow: 0px 0px 55px -45px rgba(0,0,0,0.75);
      box-shadow: 0px 0px 55px -45px rgba(0,0,0,0.75);
    }
    25%     {
      -webkit-box-shadow: 0px 0px 55px 13px #000;
      -moz-box-shadow: 0px 0px 55px 13px #000;
      box-shadow: 0px 0px 55px 13px #000;
    }
    75%     {
      -webkit-box-shadow: 0px 0px 55px -45px rgba(0,0,0,0.75);
      -moz-box-shadow: 0px 0px 55px -45px rgba(0,0,0,0.75);
      box-shadow: 0px 0px 55px -45px rgba(0,0,0,0.75);
    }
  }

  &.rubberBand {
    /*-webkit-animation-name: rubberBand;
    animation-name: rubberBand;*/
    animation:rubberBand $duration $function $count;
    -webkit-animation:rubberBand $duration $function $count;
  }

  @keyframes rubberBand {
    from {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1);
    }

    30% {
      -webkit-transform: scale3d(1.25, 0.75, 1);
      transform: scale3d(1.25, 0.75, 1);
    }

    40% {
      -webkit-transform: scale3d(0.75, 1.25, 1);
      transform: scale3d(0.75, 1.25, 1);
    }

    50% {
      -webkit-transform: scale3d(1.15, 0.85, 1);
      transform: scale3d(1.15, 0.85, 1);
    }

    65% {
      -webkit-transform: scale3d(.95, 1.05, 1);
      transform: scale3d(.95, 1.05, 1);
    }

    75% {
      -webkit-transform: scale3d(1.05, .95, 1);
      transform: scale3d(1.05, .95, 1);
    }

    to {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1);
    }
  }

  &.flipInY {
    /*-webkit-backface-visibility: visible !important;
    backface-visibility: visible !important;
    -webkit-animation-name: flipInY;
    animation-name: flipInY;*/

    animation:flipInY $duration $function $count;
    -webkit-animation:flipInY $duration $function $count;
  }

  @keyframes flipInY {
    from {
      -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
      transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
      -webkit-animation-timing-function: $function;
      animation-timing-function: $function;
      opacity: 0;
    }

    40% {
      -webkit-transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
      transform: perspective(400px) rotate3d(0, 1, 0, -20deg);
      -webkit-animation-timing-function: $function;
      animation-timing-function: $function;
    }

    60% {
      -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
      transform: perspective(400px) rotate3d(0, 1, 0, 10deg);
      opacity: 1;
    }

    80% {
      -webkit-transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
      transform: perspective(400px) rotate3d(0, 1, 0, -5deg);
    }

    to {
      -webkit-transform: perspective(400px);
      transform: perspective(400px);
    }
  }

  &.zoomOut {
    animation:zoomOut $duration $function $count;
    -webkit-animation:zoomOut $duration $function $count;

    /*-webkit-animation-name: zoomOut;
    animation-name: zoomOut;*/
  }

  @keyframes zoomOut {
    from {
      opacity: 1;
    }

    50% {
      opacity: 0;
      -webkit-transform: scale3d(.3, .3, .3);
      transform: scale3d(.3, .3, .3);
    }

    to {
      opacity: 0;
    }
  }

  &.wobble {
    animation:wobble $duration $function $count;
    -webkit-animation:wobble $duration $function $count;
  }

  @keyframes wobble {
    from {
      -webkit-transform: none;
      transform: none;
    }

    15% {
      -webkit-transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);
      transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);
    }

    30% {
      -webkit-transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 3deg);
      transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 3deg);
    }

    45% {
      -webkit-transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);
      transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);
    }

    60% {
      -webkit-transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);
      transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);
    }

    75% {
      -webkit-transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);
      transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);
    }

    to {
      -webkit-transform: none;
      transform: none;
    }
  }
}

