.premod-page{
  min-height:100%;

  font-size:1.4em;

  @media screen and (max-width: 570px) {
    font-size:1.1em;
  }

  @media screen and (max-width: 320px) {
    font-size:1em;
    line-height:1.1em;
  }

  .speakers{
      md-card{
        //height:100px;
        min-height:70px;
        background: rgba(204, 254, 252, 0.22);
        cursor:pointer;
        .inner{
          font-weght:bold;
          font-size:1.2em;
          position:relative;
          .push_counter{
            background: #ff4b3d;
            color:#fff;
            //padding:0.2em;
            border-radius: 50%;
            text-align:center;
            width: 1.5em;
            height: 1.5em;
            line-height: 1.5em;
            font-size:0.8em;
            position:absolute;
            top: -50%;
            right: -30%;
          }
        }
      }
  }

  .questions{
    div{
      margin-top:0.5em;
    }
    .question{
      margin:1em 0;
      border-bottom:1px solid  $primary-color;
      .date{
        color:$primary-color;
      }
      .fromWho{
        span{
          font-weight: bold;
        }

      }
      .text{
        margin-bottom:1em;
        .bold{
          font-weight: bold;
        }
      }
      .premod{
        text-transform: none;
        padding:0.4em;
        font-size:1em;
        //color:#fff;
        background: $pink-color;
        margin-bottom:1em;
        .md-ripple-container{
          margin: 0px !important;
        }


      }

      /*
      .active{
        background: $primary-color;
      }
      */

    }
  }

}

