.alarms-page {
  padding: 0 9px;
  background: #fff;

  .alarms {
    padding: 0 6px;

    .alarm {
      border-radius: 3px;
      border: 1px solid $primary-color;
      margin: 1em 0;

      .head{
        background: $primary-color;
        padding: 1em;
        font-size: 1.2em;
        font-weight: bold;
        margin: 0;
        color: #fff;

        .icon_{
          max-width: 32px;
          display: inline-block;
          margin-right: 10px;
        }
      }

      .card{
        padding: 1em;

        .time:before{
          //content: ' : ';
        }

        .time{
          color: #959595;
          //font-style: italic;
          overflow: hidden;
          //font-weight: bold;
          font-size: 14px;
          padding-top: 5px;
          width: 100%;

        }

        .desc{
          overflow: hidden;
          padding-top: 10px;
          padding-bottom: 0.5em;

          p{
            margin-bottom: 10px;
            margin-top: 0;
          }
        }

        .photo-list{
          padding-bottom: 1em;
        }

        .pdf-area{
          padding: 0em 0 0.3em;
          border: none;

          .title{
            margin-left: 0.5em;
            text-decoration: none;
            height: 45px;
            line-height: 45px;
            display: inline-block;
            vertical-align: top;
            color: #000;
          }

          a{
            display: inline-block;
            text-decoration: none;
            border: none;

            img{
              display: block;
              height: 45px;
            }
          }
        }

        .link-more-area{

          .link-more-label{
            color: #000;
            text-decoration: underline;
          }
        }
      }

    }



  }
}