.main-page {
  font-size:1.4em;
  @media screen and (max-width: 570px) {
    font-size:0.8em;
  }
  @media screen and (max-width: 320px) {
    font-size:0.8em;
  }

  display:flex;
  align-items:center;

  .logo_top{
    position:absolute;
    top:0;
    left:0;
    width:auto;
    height:22%;
    @media screen and (min-width:1024px) {
      display:none;
    }
  }
  .logo_top_ipad{
    position:absolute;
    top:0;
    left:0;
    width:auto;
    height:33%;
    display:none;
    @media screen and (min-width:1024px) {
      display:block;
    }
  }

  .profile{
    cursor:pointer;
    position:absolute;
    top: 35px;
    right: 10px;
    width: 35%;
    //overflow-x:auto;
    display:flex;
    align-items:center;

    @media screen and (max-width: 520px) {
      right: 25px;
    }

    @media screen and (max-width: 320px) {
      right: 11%;
    }

    @media screen and (min-width:1024px) {
      top:3vh;
      right: 7%;
      width: 25%;
      font-size:0.75em;
    }
    .icon{
      width:80px;
      height:80px;
      flex-shrink: 0;
      border:1px solid $primary-color2;
      border-radius: 50%;
      margin-right:8px;
      .mask{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1em;
        color:$primary-color2;
        border-radius: 50%;
      }
      @media screen and (max-width: 570px) {
        width:60px;
        height:60px;
      }
    }
    .title{
      color:$primary-color2;
      line-height: 1.3em;
      word-break: normal;
    }
  }
  .items{
    width:85%;
    margin:0 auto;
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width:1200px;
    @media screen and (min-width:1024px) {
      width:95%;
    }
    @media screen and (min-width:1024px) {
      justify-content: flex-end;
    }
    .noAnimation{
      animation:none!important;
      -webkit-animation:none!important;
    }
    .item{
      padding:0;
      margin:0;
      text-transform:none;
      font-size:0.95em!important;
      animation:zoomIn 1s;
      -webkit-animation:zoomIn 1s;
      &:first-child{
        margin-left:33.6%;
      }
      &:last-child{
        margin-right:33.6%;
      }
      width:32%;
      height:16vh;
      background:$gray-color;
/*      background:$light-green-color;*/
      border-radius: 1vh;
      text-align: center;
      margin-bottom:1.3vh;

      @media screen and (min-width:1024px) {
        width:18%;
        height:20vh;
        margin-right:1%;
        font-size:0.7em!important;
        &:first-child{
          margin-right:1%;
        }
        &.agenda{
          margin-left:58.4%!important;
        }
        &.speakers{
          margin:0;
        }
        &.participants{
          margin-left:19%;
        }
        &.dinner{
          margin-right:19%;
        }
        &.notices{
          margin-right:57%;
        }
      }
      .inner{
        width:100%;
        height:100%;
        display:flex;
        align-content: center;
        flex-wrap:wrap;
        justify-content: center;
        position: relative;

        .push-counter{
          position: absolute;
          left: 0;
          right: 0;
          top: calc(50% - 12px);
          height: 20px;
          line-height: 20px;
          text-align: center;
          color: $primary-color2;
          font-weight: bold;
          font-size: 1.2em;
          @media screen and (min-width:1024px) {
            top: calc(50% - 19px);
          }
        }

        .news-counter{
          position: absolute;
          right: 5px;
          top: 5px;
          height: 30px;
          width: 30px;
          line-height: 29px;
          font-size: 20px;
          border:1px solid $primary-color2;
          color: $primary-color2;
          border-radius: 50%;
          text-align: center;
          font-weight: 700;

          @media screen and (max-width:570px) {
            height: 20px;
            width: 20px;
            line-height: 21px;
            font-size: 12px;
          }
        }

        img{
          height:55%;
          width:auto;
          @media screen and (min-width:1024px) {
            height:45%;
            margin-bottom:10px;
          }
        }
        .title{
          width:100%;
          text-align: center;
          color:$primary-color2;
          padding:0 3px;
          display: inline-block;
          word-wrap:break-word;
          line-height: 1.1em;
        }
      }
    }
  }
.logo_bottom{
  position:absolute;
  bottom:0;
  right:0;
  width:auto;
  height:24%;
  @media screen and (min-width:1024px) {
    display:none;
  }
}
.logo_bottom_ipad{
    position:absolute;
    bottom:0;
    right:0;
    width:auto;
    height:33%;
    display:none;
    @media screen and (min-width:1024px) {
      display:block;
    }
}


  h2 {
    margin: 5px 0;
  }
  .icons {
    text-align: center;
    button {
      vertical-align: top;
      display: inline-block;
      width: 24%;
      margin: 5px 0.5%;
      @media screen and (max-width: 570px) {
        width: 49%;
      }
    }
  }
  table {
    td {
      border: 1px solid #f1f1f1;
      padding: 10px;
    }
  }
}
#welcome_screen {
  //background: #fff url("../img/logo.png") 95% 95% no-repeat;
  background-size: 25%;
  position: fixed;
  z-index: 300;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  .form {
    position: absolute;
    top: 50%;
    width: 60%;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    h2 {
      margin: 10px 0;
    }
    input {
      margin: 5px 0;
      display: inline-block;
      padding: 5px ;
    }
    button {
      display: block;
      border: 2px solid $primary-color2;
      background: $accent-color;
      color: #fff;
      padding: 5px 10px;
      margin-top: 10px;
    }
    p {
      color: red;
    }
  }
}
.agreement{
  padding:16px 16px 50px 16px;
  height:100%;
  .text{}
  .submit{
    margin:0 auto;
    display:flex;
    align-items:center;
    //justify-content: space-around;
    min-height:50px;
    margin:20px 0;
    md-checkbox{
      margin:0;
    }
    .btnSubmit{
      margin:0 0 0 20px;
      padding:0 5px;
      color:#fff;
      background: $primary-color2;
    }
  }
}
@keyframes zoomIn {
  from {
    opacity: 0;
  }
  50% {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes  zoomIn {
  from {
    opacity: 0;
  }
  50% {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
  to {
    opacity: 1;
  }
}