.profile-page{
  height:100%;

  font-size: 1.2em;
  @media screen and (max-width: 570px) {
    font-size: 1.1em;
  }

  @media screen and (max-width: 320px) {
    font-size: 1em;
  }
  .topTabs{
    width:100%;
    height:50px;
    text-align: center;
    display: flex;
    align-items: stretch;
    .active{
      background: rgba(239, 198, 198, 0.54) !important;
      color: $primary-color2;
      font-weight:bold;
    }

    .btnTab{
      //display:inline-block;
      //width:22%;
      flex-grow: 1;
      margin:0!important;
      padding:0!important;
      min-width:0;
      min-height:0;
      text-transform: none;
      background: $gray-color;
      border-right:1px solid rgb(239, 198, 198);
      border-bottom: 1px solid rgb(239, 198, 198);
      border-radius:0;
      &:last-child{
        border-right:none!important;
      }
      @media screen and (max-width: 320px) {
        font-size:0.7em;
      }
    }
  }
  .content{
    color:$text-color;
    height:calc(100% - 50px);
    overflow:auto;
    &.profile{
      .avaname{
        text-align: center;
        margin:16px 0 25px 0;
        .photo{
          position:relative;
          width:25vh;
          height:25vh;
          margin:0 auto;
          min-width:150px;
          min-height:150px;
          border:1px solid $primary-color;
          border-radius:50%;
          .mask{
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 5em;
            color:$primary-color;
            border-radius: 50%;
          }
          .addphoto{
            position:absolute;
            bottom:15%;right:0;
            width:40px;
            height:40px;
            border-radius: 50%;
            background: $primary-color2 url(../img/custom/plus.png) center center no-repeat;
            background-size: 80%;
            color:#fff;
            z-index:2;
            font-size:1.5em;
            line-height: 1.5em;
            @media screen and (max-width: 320px) {
              bottom:4%;
            }
            @media screen and (max-width: 570px) {
              bottom:8%;
            }
          }
        }
        .delPhoto{
          font-size:0.8em;
          text-decoration: underline;
          color:$primary-color;
          cursor:pointer;
        }
        .name{
          font-weight:bold;
          font-size:1.1em;
          color:$primary-color;
        }
      }
      .items{
        margin:0 16px;
        .item{
          display:flex;
          align-items: flex-start;
          margin-bottom:5px;
          &.status{
            margin-top:20px;
            padding-top:20px;
            border-top:1px solid red;
            margin-bottom:40px;
          }
          img{
            width:25px;
            height:25px;
          }
          .text{
            margin-left:5px;
            line-height:1.2em;
            overflow-x: auto;
            &.status{
              max-width: calc(90% - 40px);
            }
          }
          textarea{
            margin:0 0 0 5px;
            padding:0;
            width:90%;
            min-width:200px;
            border:1px solid $pink-color;
            height:80px;
            padding:5px;
            border-radius: 8px;
          }
          .btn{
            width:30px;
            height:30px;
            min-width:0;
            min-height: 0;
            border-radius: 50%;
            margin:0 0 0 10px!important;
            padding: 0!important;
            box-shadow: 0 2px 3px rgba(0,0,0,0.2);
            flex-shrink: 0;
          }
          .btnSaveStatus{
            background: rgba(239, 198, 198, 0.54) url(../img/participants/save.svg) center no-repeat;
            background-size: 60% 60%;
          }
          .btnEditStatus{
            background: rgba(239, 198, 198, 0.54) url(../img/participants/pen.svg) center no-repeat;
            background-size: 60% 60%;
          }
        }
      }
      md-input-container .md-placeholder, md-input-container label:not(.md-no-float):not(.md-container-ignore) {
        overflow: visible;
      }
    }
    &.travelcard{
      padding-top:16px;
      .myRoute{
        font-weight: bold;
        color:$primary-color2;
        padding:16px 0;
        text-align: center;
        background: $pink-color;
        //background: $green-color;
        margin-top:16px;
        margin-bottom:16px;
        //box-shadow: 0px 4px 5px rgba(0,0,0,.2);
      }
      .inner{
        background: rgb(247, 250, 248);
        box-shadow: none;
        .titleText{
          margin: 0 0 20px 0;
        }
        .title{
          font-weight: bold;
        }

        padding:8px;
        text-align:center;
      }
      .arrow{
        text-align: center;
        img{
          height:30px;
        }
      }
      .transferCard{
        margin:0;
        .header{
          font-weight: bold;
          font-size:1.1em;
          background: $green-color;
          padding:10px 0;
        }
        .form{
          padding:16px 8px;
          .item{
            margin-bottom:5px;
            .inp{
              text-align: left;
              margin:10px 0 0 0;
              width:70%;
              max-width:800px;
              min-width:250px;
            }
            .title{
              width:100%;
            }
            .text{
              font-weight: bold;
            }
            .active{
              filter:grayscale(0)!important;
            }
            .pic{
              width:50px;
              height:50px;
              -webkit-filter: grayscale(1);
              filter: grayscale(1);
              cursor:pointer;
            }
          }
          .btnSendTravelCard{
            //color:#fff;
            background: $green-color;
            margin:16px 0;
          }
        }
      }
    }
    &.neighbor{
      padding:16px;
      .title{
        line-height: 1.3em;
        text-align: center;
        //font-weight: bold;
        margin-bottom:20px;
      }
      .choice{
        display: flex;
        align-items: center;
        margin-bottom:10px;
        .btnNeighborChoice{
          background:$pink-color;
          color:$primary-color2;
          text-transform: none;
          margin-left:10px;
        }
        .person{
          margin-left:10px;
          cursor:pointer;
        }
      }
    }
    &.command{
      .comNum{
        text-align:center;
        padding:16px;
        font-size:1em;
        line-height:1.1em;
        .captain{
          display:inline!important;
          margin: 0!important;
          padding: 0!important;
          color:$primary-color;
          cursor:pointer;
          border-bottom: 1px dotted $primary-color;
        }
      }
    }
    &.eat{
      text-align: center;
      .title{
        padding:16px;
        text-align:center;
        line-height: 1.3em;
        margin-bottom:30px;
        @media screen and (max-width: 320px) {
          margin-bottom:5px;
        }
      }
      .active{
        filter:grayscale(0) !important;
      }
      .btnDish{
        display: inline-block;
        filter: grayscale(0.9);
        cursor:pointer;
        img{
          width:40vw;
          height:40vw;
          max-width:200px;
          max-height:200px;
        }
        .name{

        }
      }
      .btnSendDish{
        margin-top:50px;
        background: $primary-color;
        color:#fff;
        @media screen and (max-width: 320px) {
          margin-bottom:5px;
        }
      }
    }
  }
}

md-input-container.md-input-focused:not([md-no-float]) .md-select-placeholder span:first-child {
  -webkit-transform: none;
  transform: none;
}
